.page-main__title {
  margin: 24px auto 26px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
}

.page-main__advantages {
  margin: 0;
  padding: 0;
}

.page-main__hotel-search {
  margin: 0 auto;
  padding: 32px 20px 50px;
  min-width: 300px;
  max-width: 458px;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding: 58px 0;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 66px;
  }
}

.page-main__map {
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 350px;
  background-image: url("../img/Map-with-marker-mobile.png");
  background-image: image-set(
    url("../img/Map-with-marker-mobile.png") 1x,
    url("../img/Map-with-marker-mobile@2x.png") 2x);
  background-image: image-set(
    url("../img/Map-with-marker-mobile@2x.png") type(image/png) 2x,
    url("../img/Map-with-marker-mobile.png") type(image/png) 1x,
    url("../img/Map-with-marker-mobile@2x.webp") type(image/webp) 2x,
    url("../img/Map-with-marker-mobile.webp") type(image/webp) 1x);
  background-color: $map;

  @media (min-width: $tablet-width) {
    background-image: url("../img/Map-with-marker-tablet.png");
    background-image: image-set(
      url("../img/Map-with-marker-tablet.png") 1x,
      url("../img/Map-with-marker-tablet@2x.png") 2x);
    background-image: image-set(
      url("../img/Map-with-marker-tablet@2x.png") type(image/png) 2x,
      url("../img/Map-with-marker-tablet.png") type(image/png) 1x,
      url("../img/Map-with-marker-tablet@2x.webp") type(image/webp) 2x,
      url("../img/Map-with-marker-tablet.webp") type(image/webp) 1x);
  }
}

.page-main__form-section {
  margin: 0;
  padding: 0;
}

.page-main__form {
  margin: 0;
  padding-right: 20px;
  padding-left: 20px;

  @media (min-width: $tablet-width) {
    padding-right: 60px;
    padding-left: 60px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 120px;
    padding-left: 120px;
  }
}

.page-main__galleries {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 85px;
  }
}

.page-main__gallery {
  margin: 0;
  margin-bottom: 32px;
  padding-right: 20px;
  padding-left: 20px;

  @media (min-width: $tablet-width) {
    padding-right: 60px;
    padding-left: 60px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 0;
    padding-left: 0;
  }
}

.page-main__video-gallery {
  margin: 32px auto 0;
  width: 320px;
  max-width: 800px;

  @media (min-width: $tablet-width) {
    width: 646px;
  }

  @media (min-width: $desktop-width) {
    width: 800px;
  }
}
