@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(pt sans), url("../fonts/ptsans.woff2"), url("../fonts/ptsans.woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(pt sans), url("../fonts/ptsansbold.woff2"), url("../fonts/ptsansbold.woff");
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}

.fit-grid > * {
  min-width: 0;
}

.fit-grid img,
.fit-grid video,
.fit-grid audio,
.fit-grid canvas,
.fit-grid input,
.fit-grid select,
.fit-grid button,
.fit-grid progress {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: 1200px) {
  .non-desktop {
    display: none;
  }
}

.word-wrapper {
  display: none;
}
@media (min-width: 768px) and (max-width: 1199px) {
  .word-wrapper--tablet-only {
    display: inline;
  }
}
@media (min-width: 768px) {
  .word-wrapper--tablet-desktop {
    display: inline;
  }
}

