.button {
  padding: 5px 32px;
  box-sizing: border-box;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  color: #ffffff;
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled {
  color: #ffffff;
  background-color: #e5e5e5;
}

.main-nav {
  position: relative;
  min-height: 56px;
  font-size: 18px;
  line-height: 40px;
  font-weight: 700;
  color: #000000;
}
@media (max-width: 767px) {
  .main-nav::after {
    content: "";
    display: block;
    height: 56px;
  }
}
@media (min-width: 768px) {
  .main-nav {
    font-size: 14px;
    line-height: 26px;
  }
}

.main-nav__list {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    gap: 1px;
    background-color: #81b3d2;
  }
  .main-nav__list::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(1, 1, 1, 0.2) 100%) bottom no-repeat;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
  }
}

.main-nav__item {
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .main-nav__item {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    flex: initial;
    width: 20%;
    background-color: transparent;
  }
  .main-nav__item:nth-of-type(2), .main-nav__item:nth-of-type(6) {
    margin-right: 20%;
  }
}

.main-nav--opened .main-nav__item,
.main-nav--nojs .main-nav__item {
  display: block;
}

.main-nav__link {
  display: block;
  margin: 0;
  padding: 10px 20px 5px;
  min-height: 55px;
  height: 100%;
  box-sizing: border-box;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 767px) {
  .main-nav__link {
    box-shadow: 0 1px 0 0 rgba(1, 1, 1, 0.2), 0 1px 0 0 rgba(255, 255, 255, 0.2) inset;
  }
  .main-nav__link:hover {
    color: #ffffff;
    background-color: #669ec0;
  }
  .main-nav__link:active {
    color: rgba(255, 255, 255, 0.3);
    background-color: #5496bd;
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    margin: auto;
    padding: 0;
    width: max-content;
    max-width: calc(100% - 18px);
    color: #000000;
    box-shadow: none;
  }
  .main-nav__link:hover {
    color: #81b3d2;
  }
  .main-nav__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
}

@media (max-width: 767px) {
  .main-nav__item:first-of-type .main-nav__link {
    box-shadow: 0 1px 0 0 rgba(1, 1, 1, 0.2);
  }
}

@media (max-width: 767px) {
  .main-nav__item:last-of-type .main-nav__link {
    box-shadow: 0 1px 0 0 rgba(255, 255, 255, 0.2) inset;
  }
}

.main-nav__item--current .main-nav__link {
  color: #e5e5e5;
}
@media (min-width: 768px) {
  .main-nav__item--current .main-nav__link {
    color: #766357;
  }
  .main-nav__item--current .main-nav__link:hover {
    color: #604e43;
  }
  .main-nav__item--current .main-nav__link:active {
    color: rgba(118, 99, 87, 0.3);
  }
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--nojs .main-nav__toggle {
