.field__title {
  line-height: 26px;
  cursor: pointer;
}

.field--contact .field__title {
  padding-left: 22px;
}
@media (min-width: 1200px) {
  .field--contact .field__title {
    padding-left: inherit;
  }
}

.field__input[type=radio] + .field__title {
  position: relative;
  display: flex;
  align-items: center;
  column-gap: 22px;
  max-width: max-content;
}
.field__input[type=radio] + .field__title::before {
  content: "";
  flex-shrink: 0;
  width: 56px;
  height: 56px;
  background-color: #f2f2f2;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .field__input[type=radio] + .field__title {
    margin-left: 4px;
    column-gap: 20px;
  }
}

.field__input[type=radio]:hover + .field__title::before,
.field__input[type=radio]:focus + .field__title::before {
  background-color: #e5e5e5;
}

.field__input[type=radio]:active + .field__title::before {
  background-color: #d5d5d5;
}
.field__input[type=radio]:active + .field__title::after {
  opacity: 0.3;
}

.field__input[type=radio]:checked + .field__title::after {
  content: "";
  position: absolute;
  top: center;
  left: 17px;
  width: 22px;
  height: 22px;
  background-color: #333333;
  border-radius: 50%;
}

.field__input[type=checkbox] + .field__title {
  position: relative;
  display: flex;
  align-items: center;
  column-gap: 25px;
  max-width: max-content;
}
.field__input[type=checkbox] + .field__title::before {
  content: "";
  flex-shrink: 0;
  width: 56px;
  height: 56px;
  background-color: #f2f2f2;
  border-radius: 5px;
}

.field__input[type=checkbox]:hover + .field__title::before,
.field__input[type=checkbox]:focus + .field__title::before {
  background-color: #e5e5e5;
}

.field__input[type=checkbox]:checked + .field__title::after {
  content: "";
  position: absolute;
  top: center;
  left: 0;
  width: 56px;
  height: 56px;
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.field__input[type=checkbox]:active + .field__title::before {
  background-color: #d5d5d5;
}
.field__input[type=checkbox]:active + .field__title::after {
  opacity: 0.3;
}

