img {
  display: block;
  max-width: 100%;
  height: auto;
}

.fit-grid > * {
  min-width: 0;
}

.fit-grid img,
.fit-grid video,
.fit-grid audio,
.fit-grid canvas,
.fit-grid input,
.fit-grid select,
.fit-grid button,
.fit-grid progress {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.non-desktop {
  @media (min-width: $desktop-width) {
    display: none;
  }
}

.word-wrapper {
  display: none;

  &--tablet-only {
    @media (min-width: $tablet-width) and (max-width: $tablet-width-max) {
      display: inline;
    }
  }

  &--tablet-desktop {
    @media (min-width: $tablet-width) {
      display: inline;
    }
  }
}
